import Engine from './utils/engine';
// import Engine from './utils/engine3d';

import { trackMouse, distance, mapRange, clamp, randomBetween } from './utils/utils';

const WIDTH = 480;
const HEIGHT = 480;

const el = document.getElementById('canvas');
// const el = document.getElementById('container');

const app = new Engine(el, {
  debug: false,
  width: WIDTH,
  height: HEIGHT
});

const generateTile = (width, height, density = 1, color = '#e53935', pixelDensity = window.devicePixelRatio, ) => {
  if (!window.workerCanvas) {
    let workerCanvas = document.createElement('canvas');
    window.workerCanvas = workerCanvas;
  }

  let _width = width * pixelDensity;
  let _height = height * pixelDensity;

  workerCanvas.width = _width;
  workerCanvas.height = _height;

  const _ctx = workerCanvas.getContext('2d');

  _ctx.fillStyle = color;

  let cols = 10;
  let rows = 10;

  let size = Math.ceil(_width / cols);

  for (let x = 0; x < cols; x++) {
    for (let y = 0; y < rows; y++) {
      _ctx.fillRect(x * size, y * size, size * density, size * density);
    }
  }

  return new Promise((resolve, reject) => {
    const img = new Image();
    img.onload = () => resolve(img);
    img.src = workerCanvas.toDataURL('image/png');
  });
}

let rows = 25;
let cols = 25;
let tileImgs;
let tileSize = WIDTH / cols;

let otherTiles;

async function preload() {
  return Promise.all([
    Promise.all(new Array(10).fill(1).map((v, idx) => {
      return generateTile(tileSize, tileSize, (idx) / 9);
    })),
    Promise.all(new Array(6).fill(1).map((v, idx) => {
      return generateTile(tileSize, tileSize, (idx) / 5, 'blue');
    }))
  ])
    .then(tiles => {
      tileImgs = tiles[0];
      otherTiles = tiles[1];
    });
};

function drawTile(tile, x, y) {
  ctx.save();
  ctx.translate(x, y);
  ctx.drawImage(tile, 0, 0, tileSize, tileSize);
  // ctx.globalCompositeOperation = 'multiply';
  // ctx.fillStyle = 'red';
  // ctx.fillRect(0, 0, tileSize, tileSize);
  ctx.restore();
}

let currX = randomBetween(0, WIDTH);
let currY = randomBetween(0, HEIGHT);

let speedX = ((Math.random() - 0.5) * 2) * 3;
let speedY = ((Math.random() - 0.5) * 2) * 3;

function getIndex(x, y) {
  return clamp(
    Math.floor(
      mapRange(
        distance(currX, currY, x, y),
        0,
        500,
        tileImgs.length,
        0
      )
    ),
  tileImgs.length - 1, 0);
}


app.onRender(({ frame, width, height, debug }) => {
  ctx.fillStyle = '#ffeb3b';
  ctx.fillRect(0, 0, width, height);

  for (let x = 0; x < cols; x++) {
    for (let y = 0; y < rows; y++) {
      if (distance(x * tileSize, y * tileSize, 0, 0) < (width * Math.sqrt(2)) / 2) {
        drawTile(otherTiles[3], x* tileSize, y * tileSize, );
      }

      drawTile(tileImgs[getIndex(x * tileSize, y * tileSize)], x * tileSize, y * tileSize);
    }
  }

  currX += speedX;
  currY += speedY;

  if (currX < 0 || currX >= width) {
    currX -= speedX;
    speedX *= -1;
  }

  if (currY < 0 || currY >= height) {
    currY -= speedY;
    speedY *= -1;
  }
});

preload()
  .then(() => {
    console.log('hey');
    trackMouse();
    app.start()
  });
